.customLogin {
  background-color: lightblue;
  padding: 40px;
  flex: 1;

  display: flex;
  flex-direction: column;
  align-items: center;

  row-gap: 10px;
  border-radius: 25px;
  box-shadow: 2px 5px 10px grey;
  width: 400px;
}

.loginHeader {
  font-size: 25px;
  font-weight: bold;
  margin-bottom: 15px;
}

.email {
  border-radius: 10px;
  border-color: none;

  border: 1px solid rgb(247, 245, 245);
  width: 100%;
  font-size: 18px;
}

.email::placeholder {
  opacity: 0.5;
}

.email[type="text"] {
  padding: 12px;
}

.password[type="password"] {
  padding: 12px;
}

.password::placeholder {
  opacity: 0.5;
}

.password {
  border-radius: 10px;
  border-color: none;
  border: 1px solid rgb(247, 245, 245);
  width: 100%;
  font-size: 18px;
  margin-bottom: 15px;
}

.loginButton {
  width: 100%;
  color: white;
  background-color: black;
  border-radius: 15px;
  font-size: 18px;
  padding: 10px;
  cursor: pointer;
}

.externalLogin {
  display: flex;
  flex-direction: column;
}
